import { createRouter, createWebHashHistory } from 'vue-router';
import HomePage from '../views/HomePage.vue';
import AboutPage from '../views/About.vue';
import ParkingLots from '@/views/ParkingLots.vue';
import CollaborationPage from '../views/CollaborationPage.vue';
import MembershipPage from '../views/MembershipPage.vue';
import OnlinePayment from '../views/OnlinePayment.vue';
import RecruitmentPage from '../views/RecruitmentPage.vue';
import LoginPage from '../views/LoginPage.vue';
import UserRegister from '@/views/UserRegister.vue';
import UserRecord from '@/views/UserRecord.vue'; 
import UserCancel from '@/views/UserCancel.vue'; 
import UserApply from '@/views/UserApply.vue';
 // 在线支付组件
 // 月票申办组件
// 退租登记组件// 记录组件
 // 确保导入了组件
const routes = [




  {
    path: '/',
    name: 'HomePage',
    component: HomePage,
  },
  {
    path: '/about',
    name: 'AboutPage',
    component: AboutPage,
  },
  {
    path: '/parking-lots',
    name: 'ParkingLots',
    component: ParkingLots,
  },
  {
    path: '/collaboration-page',
    name: 'CollaborationPage',
    component: CollaborationPage,
  },
  {
    path: '/membership-page',
    name: 'MembershipPage',
    component: MembershipPage,
  },
  {
    path: '/online-payment',
    name: 'OnlinePayment',
    component: OnlinePayment,
  },
  {
    path: '/recruitment-page',
    name: 'RecruitmentPage',
    component: RecruitmentPage,
  },
  {
    path: '/login-page',
    name: 'LoginPage',
    component: LoginPage,
  },
  {
    path: '/UserRegister',
    name: 'UserRegister', // 使用多词名称
    component: UserRegister // 使用 UserRegisterForm 组件
  },
  {
    path: '/UserRecord',
    name: 'UserRecord', // 更新为多词名称
    component: UserRecord // 确保使用 UserRecord 组件
  },
  {
    path: '/UserApply',
    name: 'UserApply', // 更新为多字节名称
    component: UserApply // 确保使用 UserApply 组件
  },

  {
    path: '/UserCancel',
    name: 'UserCancel', // 更新为多字节名称
    component: UserCancel // 确保使用 UserCancel 组件

  },
  {
    path: '/OnlinePayment',
    name: 'OnlinePayment',
    component: OnlinePayment
  }

];

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(), // Change to hash mode

  routes,
});

export default router;
