<template>
    <div class="map-container">
      <!-- Parking List (Left Panel) -->
      <div class="parking-list">
        <!-- Search Bar -->
        <div class="search-bar">
          <input type="text" v-model="searchQuery" placeholder="" />
          <button @click="filterParkingLots">》</button>
        </div>
  
        <div class="parking-items">
          <!-- Message Section -->
          <div class="message-section">
            畫面顯示 {{ filteredLots.length }} 個停車場
          </div>
  
          <!-- Parking cards -->
          <div
            v-for="(lot, index) in filteredLots"
            :key="index"
            class="parking-item"
            @click="zoomToLot(lot.coords)"
          >
            <table>
              <tr>
                <td style="width: 12%">
                  <img :src="lot.image" alt="Parking Lot" />
                </td>
                <td class="text-info">
                  <h3>{{ lot.name }}</h3>
                  <p>{{ lot.spaces }} 個車位</p>
                  <p>{{ lot.address }}</p>
                </td>
                <td style="width: 20%" class="parking-type-td">
                  <button class="parking-type">{{ lot.type }}</button>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
  
      <!-- Map Container -->
      <div id="map"></div>
  
      <!-- Floating Button -->
      <div>
        <a href="/OnlinePayment" class="float-button">列表模式</a>
      </div>
    </div>
  </template>
  
  <script>
  /* global L */
  export default {
    data() {
      return {
        map: null,
        markers: [],
        searchQuery: '',
        filteredLots: [],
        parkingLots: [
          {
            name: '福安場',
            spaces: 40,
            address: '台北市仁愛路段258號',
            coords: [25.033, 121.565],
            type: '停車塔',
            image: 'https://images.pexels.com/photos/1643387/pexels-photo-1643387.jpeg',
          },
          {
            name: '安和場',
            spaces: 31,
            address: '台北市大安區安和路二段86號',
            coords: [25.034, 121.566],
            type: '平面停車',
            image: 'https://images.pexels.com/photos/1643388/pexels-photo-1643388.jpeg',
          },
          {
            name: '通化場',
            spaces: 48,
            address: '台北市大安區通化街177號',
            coords: [25.032, 121.564],
            type: '停車塔',
            image: 'https://images.pexels.com/photos/1643389/pexels-photo-1643389.jpeg',
          },
        ],
      };
    },
    mounted() {
      // Initialize the map
      this.map = L.map('map', { zoomControl: false }).setView([25.033, 121.565], 13);
  
      // Set up the OpenStreetMap tiles
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        maxZoom: 19,
        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      }).addTo(this.map);
  
      // Set initial markers and bounds
      this.filteredLots = this.parkingLots;
      this.addMarkers(this.filteredLots);
      this.fitBounds(this.filteredLots);
    },
    methods: {
      addMarkers(parkingLots) {
        // Clear existing markers
        this.markers.forEach((marker) => marker.remove());
        this.markers = [];
  
        // Add new markers
        parkingLots.forEach((lot) => {
          const customIcon = L.divIcon({
            html: `
              <div style="text-align: center;">
                <img src='https://img.icons8.com/emoji/48/000000/cat-face.png' style="width: 50px; height: 50px;" />
                <div style="font-size: 12px; color: white; background-color:green; margin-top:10px; border-radius:10px">${lot.name}</div>
              </div>
            `,
            iconSize: [50, 60],
            iconAnchor: [25, 50],
            popupAnchor: [0, -50],
          });
  
          // Create and add marker to map
          const marker = L.marker(lot.coords, { icon: customIcon })
            .addTo(this.map)
            .bindPopup(`<b>${lot.name}</b><br>${lot.address}`);
  
          this.markers.push(marker);
        });
      },
      fitBounds(parkingLots) {
        const allCoords = parkingLots.map((lot) => lot.coords);
        const bounds = L.latLngBounds(allCoords);
  
        this.map.fitBounds(bounds, {
          padding: [25, 25],
        });
      },
      filterParkingLots() {
        this.filteredLots = this.parkingLots.filter(
          (lot) =>
            lot.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
            lot.address.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
        if (this.filteredLots.length === 0) {
          alert('沒有找到符合條件的停車場');
          return;
        }
        this.addMarkers(this.filteredLots);
        this.fitBounds(this.filteredLots);
      },
      zoomToLot(coords) {
        // Center map to the coordinates of the clicked lot
        this.map.setView(coords, 17);
      },
    },
  };
  </script>
  
  <style scoped>
  
  .map-container {
    /* margin-top: 95px; */
    width: 100%;
    display: flex;
  }
  
  #map {
    height: calc(100vh - 110px);
    width: 100%;
    position: absolute;
  }
  
  .parking-list {
    position: absolute;
    top: 110px;
    left: 20px;
    z-index: 1000;
    background-color: var(--background-gray);
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    max-width: 500px;
    width: 500px;
  }
  
  .search-bar {
    padding: 10px;
    display: flex;
    justify-content: flex-start;
    background-color: white;
  }
  
  .search-bar input {
    width: 90%;
    padding: 5px;
    margin-right: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1.2rem;
  }
  
  .search-bar button {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: green;
    color: white;
    cursor: pointer;
  }
  
  .parking-items {
    background-color: var(--background-gray);
    padding: 10px;
    margin-right: 20%; /* Adjust the value as needed */
    width:92%;

    }
  
  .message-section {
    margin: 10px;
    font-size: 1.2rem;
    color: var(--font-gray);
    text-align: left;
  }
  
  .parking-item table {
    width: 100%;
    margin: 10px;
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .text-info {
    padding: 10px;
    text-align: left;
  }
  
  .text-info h3 {
    margin: 0;
    padding-top: 0;
    color: var(--font-gray);
    text-align: left;
    font-size: 1.1rem;
  }
  
  .text-info p {
    margin: 0;
    padding-top: 0;
    color: var(--font-gray);
    text-align: left;
    font-size: 0.9rem;
  }
  
  .parking-item img {
    width: 4.5rem;
    height: 4.5rem;
    border-radius: 8px;
  }
  
  .parking-type-td {
    vertical-align: top;
    text-align: right;
  }
  
  .parking-type {
    padding: 5px 10px;
    background-color: var(--primary-green);
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  /* Floating button styles */
  .float-button {
    position: fixed;
    top: 120px;
    right: 20px;
    padding: 7px 14px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: white;
    color: var(--primary-green);
    cursor: pointer;
    z-index: 1000;
  }
  
  .float-button:hover {
    background-color: #0056b3;
  }
  </style>
  