<template>
  <div class = "register_bg">
  <div class="register-container">
    <h2>註冊帳號</h2>
    <form @submit.prevent="handleSubmit">
      <!-- Single-column layout -->
      <div class="form-group">
        <label for="username">帳號（手機號碼）:</label>
        <input type="text" id="username" v-model="username" placeholder="輸入您的手機號碼作為帳號" required class="font-control" />
      </div>
      <div class="form-group">
        <label for="password">密碼:</label>
        <input type="password" id="password" v-model="password" placeholder="輸入您的密碼" required class="font-control" />
      </div>
      <div class="form-group">
        <label for="confirmPassword">再次輸入密碼:</label>
        <input type="password" id="confirmPassword" placeholder="輸入您的密碼" v-model="confirmPassword" required class="font-control" />
      </div>
      <button type="submit" class="submit-button">註冊帳號</button>
    </form>
    <p>已有帳號？<a href="/login">登入</a></p>
  </div>
</div>
<ContactFooter />
</template>

<script>
import ContactFooter from '@/components/ContactFooter.vue';


export default {
  name: 'UserRegister',
  components: {
    ContactFooter
  },

  data() {
    return {
      username: '',
      password: '',
      confirmPassword: ''
    };
  },
  methods: {
    handleSubmit() {
      if (this.password !== this.confirmPassword) {
        alert('密碼不匹配！');
        return;
      }
      console.log('註冊信息:', this.username, this.password);
    }
  }
};
</script>

<style scoped>

.register_bg {
  background-color: var(--background-gray);
  /* height: 100vh; */
}

.register-container {
  max-width: 27%;
  margin: 0px auto;
  padding: 10px;
  border-radius: 8px;
  background-color: #f9f9f9;

  padding-bottom:5rem;

}

h2 {
  /* margin:  var(--nav-first-title-margin); */
  margin:0px;
  padding-top: var(--nav-first-title-margin);
  text-align: center;
  color: var(--primary-green);
  margin-bottom:30px;
}

/* Single-column layout */
.form-group {
  margin-bottom: 30px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: var(--font-light-gray);
  font-weight: bold;
  text-align: left; /* Align label to the left */
}

input[type="text"],
input[type="password"] {
  width: 100%; /* Full width for single column */
  padding: 0.8rem;
  border: 3px solid var(--primary-green);
  border-radius: 10px;
  box-sizing: border-box;
}

.font-control {
  color: var(--font-gray);
  font-size: 1.2rem;
}

/* Submit button style */
.submit-button {
  margin-top:4rem;
  width:100%;
  display: block;
  /* margin: 20px auto; */
  padding: 12px 50px;
  background-color: #008000;
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 1.2rem;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #005f00;
}

p {
  text-align: center;
}

a {
  color: var(--primary-green);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
</style>
