<script>
export default {
  name: 'ParkingManagementForm',
  data() {
    return {
      formData: {
        parkingType: '平面空地',
        parkingSpaces: '50個以下',
        parkingStatus: '自行經營',
        customerName: '',
        clientName: '',
        contactPhone: '',
        contactPerson: '',
        contactTime: '',
        otherInfo: '',
        equipment: [],
        rentalPeriod: '短期六~十二個月內',
        consignorIdentity: '個人',
        organizationType: '同業聯盟',
        location: '',
        name: '',
        phone: '',
        email: '',
        message: ''
      },
      equipmentOptions: [
        { value: 'none', label: '無設備' },
        { value: 'management', label: '管理亭或管理室' },
        { value: 'ticketing', label: '自動按紐取票機' },
        { value: 'payment', label: '自動繳費機' },
        { value: 'computer', label: '電腦計費系統' },
        { value: 'camera', label: '監視器' },
        { value: 'gate', label: '驗票機與讀卡機' },
      ]
    }
  },
  methods: {
    toggleEquipment(value) {
      const index = this.formData.equipment.indexOf(value);
      if (index === -1) {
        this.formData.equipment.push(value);
      } else {
        this.formData.equipment.splice(index, 1);
      }
    },
    submitForm() {
      console.log('Form submitted:', this.formData)
      // 这里可以添加发送数据到服务器的逻辑
    }
  }
}
</script>

