<template>
  <div id="app">
    <AppNavbar />
    <router-view />
  </div>
</template>

<script>
import AppNavbar from './components/AppNavbar.vue'


export default {
  name: 'App',
  components: {
    AppNavbar
  },

  async mounted() {

    //counter zooming
    this.adjustScale();
    // window.addEventListener('resize', this.adjustScale);
  },  
  methods: {

    getZoomLevel() {

      const desiredWidth = 1943  ;
      // alert("window.innerWidth: " + window.innerWidth + "window.outerWidth: " + window.outerWidth);
      // return window.outerWidth / window.innerWidth;
      if (window.innerWidth > desiredWidth) {
        // return 1;
        return desiredWidth / window.innerWidth;

      }
      else {
        return desiredWidth / window.innerWidth;
      }
      // return 1500 / window.innerWidth;
      // return 1;
    },
    adjustScale() {
      const zoomLevel = this.getZoomLevel();
      document.body.style.transform = `scale(${1 / zoomLevel})`;
      document.body.style.transformOrigin = '0 0';
      document.body.style.width = `${100 * zoomLevel}%`;
      // document.body.style.height = `${100 * zoomLevel}%`;

      // alert(`Zoom level: ${zoomLevel}` + `window.innerWidth: ${window.innerWidth}` + `window.outerWidth: ${window.outerWidth}`);

    }

  }
}
</script>

<style>
/* 全局样式 */
html {
  scroll-behavior: smooth;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
  padding: 0px;
  width: 1920px;
}
</style>
