<template>
  <div class="cancel-container">
    <h3>退租登記</h3>
    <p>退租車位需填寫以下表格內容，再送出登記</p>

    <!-- 退租登记表单 -->
    <div class="form-content">
      <form @submit.prevent="handleSubmit">
        <div class="form-card">
          <h4>基本信息</h4>
          <div class="two-columns">
            <div class="form-group">
              <label for="name">*姓名</label>
              <input type="text" id="name" v-model="name" placeholder="輸入姓名" required class="font-control" />
            </div>
            <div class="form-group">
              <label for="carNumber">*車號</label>
              <input type="text" id="carNumber" v-model="carNumber" placeholder="範例：abc1234(不需加上'_'符號)" required
                class="font-control" />
            </div>
            <div class="form-group">
              <label for="phone">*聯絡電話</label>
              <input type="text" id="phone" v-model="phone" placeholder="範例：0920" required class="font-control" />
            </div>
            <div class="form-group">
              <label for="email">*電子郵件</label>
              <input type="email" id="email" v-model="email" placeholder="輸入電子郵件" required class="font-control" />
            </div>

            <div class="form-group">
              <label for="idNumber">*身分證字號/統一編號</label>
              <input type="text" id="idNumber" v-model="idNumber" placeholder="若為法人請填入統一編號" required
                class="font-control" />
            </div>
            <div class="form-group">
              <label for="parkingType">停車場形式</label>
              <select id="parkingType" v-model="parkingType" class="form-select font-control">
                <option value="平面空地">平面空地</option>
                <option value="立體停車場">立體停車場</option>
                <option value="地下停車場">地下停車場</option>
              </select>
            </div>
          </div>
        </div>
      </form>

      <!-- 付款信息表单 -->
      <form @submit.prevent="handlePaymentSubmit">
        <div class="form-card">
          <h4>匯款資料(*號為必填欄位)</h4>

          <div class="two-columns">
            <div class="form-group">
              <label for="accountName">*戶名（需與承租人姓名相同）</label>
              <input type="text" id="accountName" v-model="accountName" placeholder="輸入戶名" required
                class="font-control" />
            </div>
            <div class="form-group">
              <label for="bankCode">*銀行代碼</label>
              <select id="bankCode" v-model="bankCode" required class="form-select font-control">
                <option value="">請選擇銀行代碼</option>
                <option value="001">台灣銀行</option>
                <option value="002">中國信託</option>
              </select>
            </div>
            <div class="form-group">
              <label for="branchCode">*分行代碼</label>
              <select id="branchCode" v-model="branchCode" required class="form-select font-control">
                <option value="">請選擇分行名稱</option>
                <option value="001">台北分行</option>
                <option value="002">高雄分行</option>
              </select>
            </div>
            <div class="form-group">
              <label for="accountNumber">匯款帳號：（需與戶名及身分證相同之帳號）</label>
              <input type="text" id="accountNumber" v-model="accountNumber" placeholder="請輸入帳號" required
                class="font-control" />
            </div>
          </div>

          <!-- <p>請保留此存款帳號憑證資料，謝謝</p> -->
        </div>
        <div class= 'submit-btns'>
        <button type="submit" class="reset-button">重填</button>
        <button type="submit" class="submit-button">送出登記</button>
      </div>
      </form>
    </div>
  </div>
  <ContactFooter />
</template>

<script>
import ContactFooter from '@/components/ContactFooter.vue';

export default {
  name: 'UserCancel',
  components: {
    ContactFooter
  },
  data() {
    return {
      idNumber: '',
      parkingType: '平面空地',
      name: '',
      carNumber: '',
      phone: '',
      email: '',
      accountName: '',
      bankCode: '',
      branchCode: '',
      accountNumber: ''
    };
  },
  methods: {
    handleSubmit() {
      console.log('退租登记信息:', this.idNumber, this.parkingType, this.name, this.carNumber, this.phone, this.email);
    },
    handlePaymentSubmit() {
      console.log('付款信息:', this.accountName, this.bankCode, this.branchCode, this.accountNumber);
    }
  }
};
</script>

